import React from 'react'
import PropTypes from 'prop-types'
import CircularProgress from '@material-ui/core/CircularProgress'
import axios from 'axios'
import Img from 'react-image'
import Utils from '../../Utils'
import Config from '../../Config'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Button from '@material-ui/core/Button'
import FBPostEditDialog from '../../components/FBPostEditDialog'

const classes = {
  cardStyle: {
    maxWidth: '800px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

const imgStyle = {
  height: '300px',
  maxWidth: '100%',
}

const ImgLoader = () => <div style={imgStyle}><center><br/><br/><CircularProgress /></center></div>

class PostList extends React.Component {
  constructor(props) {
    super()
    this.props = props
    this.state = {
      loading: true,
      posts: [],
      editingPost: null,
    }
    window.changeHeader('Bài đã lên lịch - TongHopEvent')
  }

  static propTypes = {
    loadFeedEndpoint: PropTypes.string.isRequired,
    editPostEndpoint: PropTypes.string.isRequired,
    deletePostEndpoint: PropTypes.string.isRequired,
    timeFieldName: PropTypes.string.isRequired,
    newestFirst: PropTypes.bool,
  };

  componentDidMount() {
    this.loadData()
  }

  async loadData() {
    const { newestFirst } = this.props;
    this.setState({loading: true, editingPost: null})
    const token = Utils.getLocalStorage('token')
    const res = await axios.get(`${this.props.loadFeedEndpoint}?token=${token}`)
    if (res.data.error)
      this.setState({loading: false, posts: []})
    else {
      const arr = newestFirst ? res.data : res.data.reverse()
      this.setState({loading: false, posts: this.processTimeField(arr) })
    }
  }

  processTimeField = (data) => {
    if (this.props.timeFieldName === 'created_time') {
      return data.map(p => ({
        ...p,
        created_time: (new Date(p.created_time)).getTime() / 1000,
      }))
    } else {
      return data
    }
  }

  deletePost = (post) => async () => {
    if (window.confirm('Bạn có chắc chắn xóa?')) {
      this.setState({loading: true})
      const token = Utils.getLocalStorage('token')
      await axios.delete(`${this.props.deletePostEndpoint}/${post.id}?token=${token}`)
      this.loadData()
    }
  }

  openOnFB = (post) => () => {
    window.open(`https://www.facebook.com/${post.id}`, '_blank').focus();
  }

  onEditDialogClose = (shouldReload) => {
    if (shouldReload) {
      this.loadData()
    } else {
      this.setState({editingPost: null})
    }
  }

  _renderMainView() {
    const { timeFieldName } = this.props;

    return this.state.posts.map((post, i) => {
      const isFromUser = post.from && post.from.id !== Config.SELF_PAGE_ID;
      return <div style={classes.cardStyle} key={post.id}>
        <Card>
          <CardContent>
            <p><b>{Utils.getTimeStr(post[timeFieldName] * 1000)} ngày {Utils.getDateStr(post[timeFieldName] * 1000)}</b></p>
            <Img alt=''
              src={post.full_picture}
              style={imgStyle}
              loader={<ImgLoader />}
            />
            {isFromUser && <p style={{
                backgroundColor: '#eee',
                padding: '10px',
                fontWeight: 'bold',
              }}
              children="(Bài viết này do độc giả đăng lên page)"
            />}
            <p>
              {Utils.nl2br(post.expanded ? post.message : post.message.substring(0, 250))}
              <br/>
              <a href="#" onClick={(e) => {
                e.preventDefault()
                this.setState({posts: this.state.posts.map((p, j) => {
                  const cloned = JSON.parse(JSON.stringify(p))
                  if (i === j) cloned.expanded = !cloned.expanded
                  return cloned
                })})
              }}>{post.expanded ? 'Thu gọn' : '... Đọc thêm'}</a><br/>
            </p>
            <p>
              <Button color="primary" variant="outlined" onClick={this.deletePost(post)}>Xóa</Button>
              &nbsp;&nbsp;
              <Button color="primary" variant="outlined" onClick={() => this.setState({ editingPost: post })} disabled={isFromUser}>Sửa</Button>
              &nbsp;&nbsp;
              <Button color="primary" variant="outlined" onClick={this.openOnFB(post)}>Xem trên FB</Button>
            </p>
          </CardContent>
        </Card>
      </div>
    })
  }

  _renderDocNotFound() {
    return (
      <center>
        <br/><br/>
        (Không có dữ liệu)<br/>
        <br/><br/>
      </center>
    )
  }

  render() {
    const { editingPost, loading, posts } = this.state

    const loadingComponent = <div>
      <br/><br/><br/>
      <center><CircularProgress /></center>
    </div>

    return <>
      {editingPost && <FBPostEditDialog
        post={editingPost}
        onClose={this.onEditDialogClose}
        editPostEndpoint={this.props.editPostEndpoint}
      />}
      {loading ? loadingComponent : 
        posts.length > 0 ? this._renderMainView() : this._renderDocNotFound()
      }
    </>
  }
}

export default PostList