import React from 'react'
import PostList from './PostList'
import Config from '../../Config'

const Scheduled = () => {
  return <PostList
    loadFeedEndpoint={`${Config.BACKEND}/publish/scheduled`}
    editPostEndpoint={`${Config.BACKEND}/publish/post`}
    deletePostEndpoint={`${Config.BACKEND}/publish/post`}
    timeFieldName="scheduled_publish_time"
    newestFirst={false}
  />
}

export default Scheduled