import React from 'react';
import Theme from './Theme';
import LoginLayout from './layouts/Login';
import HomeLayout from './layouts/Home';
import ViewLayout from './layouts/View';
import PublishLayout from './layouts/Publish';
import GetImageLayout from './layouts/GetImage';
import './App.css';
import './bootstrap.css';
import { HashRouter, Route, Switch } from 'react-router-dom';
import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';
import Alert from './components/Alert';
import Scheduled from './layouts/fb/Scheduled';
import FBFeed from './layouts/fb/FBFeed';
import Settings from './layouts/Settings';
import Messenger from './components/messenger';
import Sidebar from './components/sidebar/Sidebar';
import Measure from './components/Measure';
import Config from './Config';
import Utils from './Utils';

class Main extends React.Component {
  constructor() {
    super()
    this.state = {
      header: 'Home',
      publishData: null,
    }
  }

  changeHeader(header) {
    if (this.state.header === header) return
    else this.setState({header})
  }

  gotoPublish(data, publishAction) {
    this.setState({publishData: data})
    window.location.hash = '#/' + publishAction
  }

  Home = () => {
    return <HomeLayout
      gotoPublish={this.gotoPublish.bind(this)}
    />
  }

  View = ({match}) => {
    return <ViewLayout
      id={match.params.id}
    />
  }

  Publish = () => {
    return <PublishLayout
      data={this.state.publishData}
    />
  }

  GetImage = () => {
    return <GetImageLayout
      data={this.state.publishData}
      gotoPublish={this.gotoPublish.bind(this)}
    />
  }

  Copyright = () => {
    return <center>
      <br/><br/>
      Developed by&nbsp;
      <b><a href="https://ngxson.com" target="_blank" rel="noopener noreferrer">Nui</a></b><br/>
      Version {Config.VERSION}
      <br/><br/>
    </center>
  }

  Logout = () => {
    Utils.removeLocalStorage('token')
    window.location.href = '#/'
    window.location.reload()
    return null
  }

  render() {
    window.changeHeader = this.changeHeader.bind(this)

    return (
      <>
        <Route path="/manage" component={this.Home} />
        <Route path="/login" component={LoginLayout} />
        <Route path="/view/:id" component={this.View} />
        <Route path="/publish" component={this.Publish} />
        <Route path="/getimage" component={this.GetImage} />
        <Route path="/scheduled" component={Scheduled} />
        <Route path="/fbfeed" component={FBFeed} />
        <Route path="/settings" component={Settings} />
        <Route path="/logout" component={this.Logout} />
        <this.Copyright />
      </>
    );
  }
}

const RedirectToHome = () => {
  window.location.href = Utils.getLocalStorage('token')
    ? '#/msg'
    : '#/login'
  return null
}

function App() {
  return (
    <HashRouter>
      <MuiThemeProvider theme={Theme}>
        <Sidebar>
          <div className="mainView">
            <Switch>
              <Route exact path="/" component={RedirectToHome} />
              <Route path="/msg" component={Messenger} />
              <Route component={Main} />
            </Switch>
          </div>
          <Alert />
        </Sidebar>
        <Measure />
      </MuiThemeProvider>
    </HashRouter>
  );
}

export default App;