import React from 'react';
import clsx from 'clsx';
import { useTheme } from '@material-ui/core/styles';
import { useSidebarStyles } from './styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { getCurrentMenuItem, SidebarItems } from './SidebarItems';
import { withRouter } from 'react-router';

function Sidebar({children, location: {pathname}}) {
  const classes = useSidebarStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const currentMenuItem = getCurrentMenuItem('#' + pathname);
  const isPublicView = pathname.match(/\/(view\/|login)/);

  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  const handleClickItem = (item) => () => {
    const { href } = item;
    if (href.startsWith('http')) {
      window.open(href, '_blank').focus(); // open in new tab
    } else {
      window.location.href = href;
    }
  }

  return (
    <div className={classes.root}>
      <CssBaseline />
      <AppBar
        position="fixed"
        style={{ zIndex: open ? 80 : 100 }}
        className={`${classes.appBar} ${open ? classes.appBarShift : ''}`}
      >
        <Toolbar>
          {!isPublicView && <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={`${classes.menuButton} ${open ? classes.hide : ''}`}
          >
            <MenuIcon />
          </IconButton>}
          <Typography variant="h6" noWrap>
            {currentMenuItem.title}
          </Typography>
        </Toolbar>
      </AppBar>
      <Drawer
        style={{ zIndex: 90 }}
        variant={isPublicView ? 'temporary' : 'permanent'}
        className={`${classes.drawer} ${open ? classes.drawerOpen : classes.drawerClose}`}
        classes={{
          paper: open ? classes.drawerOpen : classes.drawerClose,
        }}
      >
        <div className={classes.toolbar}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
          </IconButton>
        </div>
        <Divider />
        {SidebarItems.map((obj, i) => {
          if (obj.list && !obj.hidden) {
            return <List key={i}>
              {obj.list.map((item, j) => (
                <ListItem
                  button
                  selected={currentMenuItem.href === item.href}
                  onClick={handleClickItem(item)} key={j}
                >
                  <ListItemIcon>{item.icon}</ListItemIcon>
                  <ListItemText primary={item.title} />
                </ListItem>
              ))}
            </List>
          } else if (obj.separator) {
            return <Divider key={i} />;
          }
        })}
      </Drawer>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        {children}
      </main>
    </div>
  );
}

export default withRouter(Sidebar);