import React from 'react';
import {
  Forum,
  Receipt,
  EventAvailable,
  Help,
  CheckCircle,
  ExitToApp,
  ListAlt,
  Settings,
} from '@material-ui/icons';

export const SidebarItems = [
  { list: [
    {
      title: 'Inbox',
      href: '#/msg',
      icon: <Forum />,
    },
    {
      title: 'Quản lý',
      href: '#/manage',
      icon: <Receipt />,
    },
    {
      title: 'Bài đã lên lịch',
      href: '#/scheduled',
      icon: <EventAvailable />,
    },
    {
      title: 'Bài đã đăng',
      href: '#/fbfeed',
      icon: <ListAlt />,
    }
  ] },
  { separator: true },
  { list: [
    {
      title: 'Hướng dẫn',
      href: 'https://blog.ngxson.com/tonghopevent/',
      icon: <Help />,
    },
    {
      title: 'Điều khoản sử dụng',
      href: 'https://sites.google.com/view/tonghopevent/',
      icon: <CheckCircle />,
    }
  ] },
  { separator: true },
  { list: [
    {
      title: 'Tùy chỉnh',
      href: '#/settings',
      icon: <Settings />,
    },
    {
      title: 'Đăng xuất',
      href: '#/logout',
      icon: <ExitToApp />,
    }
  ] },
  { list: [
    {
      title: 'Tổng hợp các dự án và cuộc thi',
      href: '#/',
      icon: null,
    },
    {
      title: 'Đăng bài (Lấy ảnh)',
      href: '#/getimage',
      icon: null,
    },
    {
      title: 'Đăng bài',
      href: '#/publish',
      icon: null,
    },
    {
      title: 'Xem bài viết',
      href: '#/view',
      icon: null,
    }
  ], hidden: true }
];

export const getCurrentMenuItem = (currentHash) => {
  for (const { list } of SidebarItems) {
    if (!list) continue;
    for (const item of list) {
      if (currentHash.startsWith(item.href)) return item;
    }
  }
  return null;
}
