import React from 'react';
import {
  ConversationHeader,
  Avatar,
  InfoButton,
  Button,
} from "@chatscope/chat-ui-kit-react";
import { withMessengerContext } from './Context';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope } from "@fortawesome/free-solid-svg-icons/faEnvelope";
import { faEnvelopeOpenText } from "@fortawesome/free-solid-svg-icons/faEnvelopeOpenText";
import Config from '../../Config';

const MarkButton = withMessengerContext(({psid, unreadMark, markRead, markUnread}) => {
  const btnClick = () => {
    if (unreadMark[psid]) {
      markRead(psid);
    } else {
      markUnread(psid);
    }
  }

  return (
    <Button
      className={'cs-button cs-button--info'}
      style={{marginRight: '20px'}}
      icon={<FontAwesomeIcon icon={unreadMark[psid] ? faEnvelopeOpenText : faEnvelope} />}
      onClick={btnClick}
    ></Button>
  );
});

const Header = ({loading, profile, openConversationOnFB, openMainMenu, psid}) => {
  return loading
    ? (
      <ConversationHeader>
        <ConversationHeader.Back onClick={openMainMenu} />
        <ConversationHeader.Content userName="Loading..." />        
      </ConversationHeader>
    ) : (
      <ConversationHeader>
        <ConversationHeader.Back onClick={openMainMenu} />
        <Avatar
          src={profile.profile_pic}
          name={profile.name}
          onClick={openConversationOnFB}
        />
        <ConversationHeader.Content userName={profile.name} />
        <ConversationHeader.Actions>
          {Config.INBOX_UNREAD_ABILITY && (
            <MarkButton
              as="InfoButton"
              psid={psid}
            />
          )}
          <InfoButton onClick={openConversationOnFB} />
        </ConversationHeader.Actions>          
      </ConversationHeader>
    );
}

export default Header;