import React from 'react';
import {
  ConversationList,
  Conversation,
} from "@chatscope/chat-ui-kit-react";
import { removeVietnameseTones, mergeNewConversations, getTargetId } from './utils';
import Socket, { makeRequest } from './socket';
import Config from '../../Config';
import { withMessengerContext } from './Context';

class Conversations extends React.Component {
  state = {
    ready: false,
    loading: false,
    conversations: [],
    next: null,
  };

  openConversation = (id, name) => () => {
    const n = removeVietnameseTones(name.toLowerCase()).replace(/[^a-z0-9]+/g, '-');
    window.location.hash = `#/msg/${id}/${n}`;
  }

  componentDidMount() {
    this.loadMoreConversations();
    Socket.subscribeInbox(this.inboxUpdateHandler);
  }

  componentWillUnmount() {
    Socket.unsubscribeInbox(this.inboxUpdateHandler);
  }

  inboxUpdateHandler = async (ev) => {
    const { data: newArr } = await makeRequest('get', '/conversations', {limit: 5});
    this.setState((prevState) => ({
      conversations: mergeNewConversations(prevState.conversations, newArr),
    }));
  }

  async loadMoreConversations() {
    if (!this.state.loading) {
      this.setState({ loading: true });

      const qs = {};
      if (this.state.next) qs.after = this.state.next;
      let { data, paging } = await makeRequest('get', '/conversations', qs);

      if (Config.PROD) {
        data = data.filter(c => c.id !== Config.INBOX_IGNORED_CONVERSATION);
      }

      if (data) this.setState({
        conversations: [...this.state.conversations, ...data],
        loading: false,
        ready: true,
        next: (paging?.next) ? (paging?.cursors?.after) : null,
      });
    }
  }

  onYReachEnd = () => {
    if (this.state.next && !this.state.loading) {
      this.loadMoreConversations();
    }
  }

  render() {
    const { unreadMark } = this.props;

    return (
      <ConversationList
        loading={!this.state.ready}
        loadingMore={this.state.ready && this.state.loading}
        onYReachEnd={this.onYReachEnd}
      >
        {this.state.conversations.map(conv => {
          const name = conv.senders.data[0].name;
          const psid = getTargetId(conv);
          return (
            <Conversation
              key={conv.id}
              name={`${name} (${conv.message_count})`}
              info={conv.snippet}
              onClick={this.openConversation(conv.id, name)}
              active={this.props.tid === conv.id}
              unreadDot={!!unreadMark[psid]}
            ></Conversation>
          );
        })}
      </ConversationList>
    );
  }
}

export default withMessengerContext(Conversations);