import React, { useEffect, useRef } from 'react';

const updateViewportSize = (div) => {
  if (!div) return;
  window.NUI_VIEW_HEIGHT = div.clientHeight - 64;
  window.NUI_VIEW_WIDTH = div.clientWidth;
};

const Measure = () => {
  const elem = useRef(null);

  useEffect(() => {
    if (elem && elem.current) {
      setInterval(() => {
        updateViewportSize(elem.current);
      }, 1000);
    }
  }, [elem]);

  return (
    <div ref={elem} style={{
      position: 'fixed',
      top: 0,
      left: 0,
      height: '100%',
      width: '100%',
      pointerEvents: 'none',
      //border: '2px solid red',
      zIndex: 1000,
    }}></div>
  );
}

export default Measure;