import React from 'react'
import Utils from '../Utils'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Switch from '@material-ui/core/Switch'
import Button from '@material-ui/core/Button'


const classes = {
  cardStyle: {
    maxWidth: '800px',
    marginTop: '20px',
    marginRight: 'auto',
    marginLeft: 'auto',
  }
}

class Settings extends React.Component {
  state = {
    ...Utils.settings,
  }

  saveSettings = () => {
    Utils.setSettings({
      ...this.state,
    });
    window.toastr.success('Đã lưu cài đặt');
  }

  render() {
    const { markReadOnOpenInbox } = this.state;

    return (
      <div style={classes.cardStyle}>
        <Card>
          <CardContent>
            <p>
              <h2>Cài đặt</h2><br/>
              Lưu ý: Các cài đặt sau sẽ được lưu trên trình duyệt này. Nếu bạn đổi thiết bị khác, bạn cần chỉnh lại cho thiết bị đó.
            </p>
            <FormControlLabel
              control={
                <Switch
                  checked={markReadOnOpenInbox}
                  onChange={() => this.setState({ markReadOnOpenInbox: !markReadOnOpenInbox })}
                  color="primary"
                />
              }
              label={'Đánh dấu "đã đọc" khi bấm vào đọc inbox'}
            />
            <br/>
            <br/>
            <Button
              variant='outlined'
              onClick={this.saveSettings}
            >Lưu cài đặt</Button>
          </CardContent>        
        </Card>
      </div>
    )
  }
}

export default Settings