import React from 'react';
import Socket, { makeRequest } from './socket';
import Config from '../../Config';

export const MessengerContext = React.createContext();

export class MessengerContextProvider extends React.Component {
  state = {
    unreadMark: {},
  };

  componentDidMount() {
    if (Config.INBOX_UNREAD_ABILITY) {
      this.fetchUnread();
    }
  }

  fetchUnread = async () => {
    const res = await makeRequest('get', '/unread');
    const unreadMark = {};
    for (const { id } of res) {
      unreadMark[id] = true;
    }
    this.setState({ unreadMark });
  }

  markUnread = (psid) => {
    Socket.getSocket().emit('mark_unread', psid);
    const unreadMark = {...this.state.unreadMark};
    unreadMark[psid] = true;
    this.setState({ unreadMark });
  }

  markRead = (psid) => {
    Socket.getSocket().emit('mark_read', psid);
    const unreadMark = {...this.state.unreadMark};
    delete unreadMark[psid];
    this.setState({ unreadMark });
  }

  render() {
    return (
      <MessengerContext.Provider value={{
        unreadMark: this.state.unreadMark,
        markUnread: this.markUnread,
        markRead: this.markRead,
      }}>
        {this.props.children}
      </MessengerContext.Provider>
    );
  }
}


export function withMessengerContext(Component) {
  return function HOC(props) {
    return (
      <MessengerContext.Consumer>
        {(contexts) => <Component {...props} {...contexts} />}
      </MessengerContext.Consumer>
    )
  }
}
