import React from 'react'
import PostList from './PostList'
import Config from '../../Config'

const FBFeed = () => {
  return <PostList
    loadFeedEndpoint={`${Config.BACKEND}/fbfeed`}
    editPostEndpoint={`${Config.BACKEND}/fbfeed`}
    deletePostEndpoint={`${Config.BACKEND}/fbfeed`}
    timeFieldName="created_time"
    newestFirst={true}
  />
}

export default FBFeed