import React from 'react';
import Conversations from './Conversations';
import MessageView from './MessageView';
import Config from '../../Config';
import { HashRouter, Route, withRouter } from 'react-router-dom';
import "@chatscope/chat-ui-kit-styles/dist/default/styles.min.css";
import {
  MainContainer,
  Sidebar,
  ChatContainer,
} from "@chatscope/chat-ui-kit-react";
import { MessengerContextProvider } from './Context';

class Messenger extends React.Component {
  state = { tid: null, viewHeight: '100vh' };

  updateHeight = () => {
    if (window.NUI_VIEW_HEIGHT && this.state.viewHeight !== window.NUI_VIEW_HEIGHT) {
      this.setState({ viewHeight: window.NUI_VIEW_HEIGHT });
    }
  }

  componentDidMount() {
    this.intervalId = setInterval(this.updateHeight, 1000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  MessageView = () => {
    const { tid, viewHeight } = this.state;
    const { goBackTo } = this.props;
    return <div style={{ position: "relative", height: viewHeight }} className={!tid ? 'home' : 'chat'}>
      <MainContainer>
        <Sidebar position="left" scrollable={true}>
          <Conversations tid={tid} />
        </Sidebar>
        {tid
          ? <MessageView tid={tid} goBackTo={goBackTo} />
          : <ChatContainer />
        }
      </MainContainer>
      <Config.CustomMessengerWidget />
    </div>;
  }

  MessageTidChanged = ({match}) => {
    if (match.params.tid !== this.state.tid)
      this.setState({ tid: match.params.tid });
    return null;
  }

  MessageTidReset = () => {
    if (this.state.tid !== null)
      this.setState({ tid: null });
    return null;
  }

  render() {
    return (
      <HashRouter>
        <MessengerContextProvider>
          <Route path="/msg" component={this.MessageView} />
          <Route exact path="/msg" component={this.MessageTidReset} />
          <Route exact path="/msg/:tid/:name" component={this.MessageTidChanged} />
        </MessengerContextProvider>
      </HashRouter>
    );
  }
}

export default withRouter(Messenger);