import { io } from 'socket.io-client';
import Utils from '../../Utils';
import Config from '../../Config';

class Socket {
  static socket = null;
  static getSocket() {
    if (!Config.SOCKETIO) return null;
    if (!Socket.socket) {
      Socket.socket = io(Config.SOCKETIO, {
        transports: ['websocket'],
        query: `module=${Config.SOCKETIO_MODULE}&token=${Utils.getToken()}`,
      });
      Socket.socket.on('connect', () => console.log('connected'));
    }
    return Socket.socket;
  }

  static subscribeInbox(handler) {
    const socket = Socket.getSocket();
    if (socket) socket.on('inbox', handler);
  }

  static unsubscribeInbox(handler) {
    const socket = Socket.getSocket();
    if (socket) socket.offAny(handler);
  }
}

function uuidv4() {
  return ([1e7]+-1e3+-4e3+-8e3+-1e11).replace(/[018]/g, c =>
    (c ^ crypto.getRandomValues(new Uint8Array(1))[0] & 15 >> c / 4).toString(16)
  );
}

/**
 * make HTTP requests via socket.io
 * @param {*} method 
 * @param {*} endpoint 
 * @param {*} query 
 * @param {*} body 
 */
export const makeRequest = (method, endpoint, query = {}, body = {}) => new Promise(resolve => {
  if (!Config.PROD) console.log(method, endpoint, query, body);
  const socket = Socket.getSocket();
  const requestId = uuidv4();
  const callback = (data) => {
    socket.offAny(callback);
    resolve(data);
  };
  socket.on(requestId, callback);
  socket.emit(`${method.toLowerCase()} ${endpoint.replace(/\/$/, '')}`, {
    requestId,
    query,
    body,
  });
});

export default Socket;